@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Black.woff2") format('woff2'), url("../fonts/Roboto-Black.woff") format('woff');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Regular.woff2") format('woff2'), url("../fonts/Roboto-Regular.woff") format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Bold.woff2") format('woff2'), url("../fonts/Roboto-Bold.woff") format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
* {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-tap-highlight-color: transparent;
}
*,
:after,
:before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style-type: none;
  -webkit-padding-start: 0;
  padding-inline-start: 0;
}
input::-ms-clear {
  display: none;
}
a,
button {
  display: inline-block;
  box-sizing: border-box;
  padding: 0;
  color: inherit;
  font: inherit;
  text-decoration: none;
  text-align: left;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
:root {
  --font-page: "Roboto", sans-serif;
  --min-width: 375px;
  --max-width: 1920px;
  --sm-width: 576px;
  --md-width: 768px;
  --lg-width: 992px;
  --xl-width: 1280px;
  --xxl-width: 1560px;
  --sm-padding: 20px;
  --md-padding: 29.5px;
  --xl-padding: 153px;
  --xxl-padding: 300px;
  --color-white: #ffffff;
  --color-white-050: #ffffff80;
  --color-black-01: #000000;
  --color-black-02: #1E0801;
  --color-grey: #f4f4f4;
  --color-accent: #dda046;
  --color-font-extra-dark: var(--color-black-01);
  --color-font-dark: var(--color-black-02);
  --color-font-light: var(--color-white);
  --color-back-light: var(--color-grey);
  --color-back-extra-dark: var(--color-black-01);
  --color-back-dark: var(--color-black-02);
}
.wrapper-width {
  width: 100%;
}
@media (min-width: 1280px) {
  .wrapper-width {
    max-width: 1920px;
    margin-right: auto;
    margin-left: auto;
  }
}
.wrapper-padding {
  padding-right: var(--sm-padding);
  padding-left: var(--sm-padding);
}
@media (min-width: 768px) {
  .wrapper-padding {
    padding-right: var(--md-padding);
    padding-left: var(--md-padding);
  }
}
@media (min-width: 1280px) {
  .wrapper-padding {
    padding-right: var(--xl-padding);
    padding-left: var(--xl-padding);
  }
}
@media (min-width: 1560px) {
  .wrapper-padding {
    margin-right: auto;
    margin-left: auto;
    padding-right: var(--xxl-padding);
    padding-left: var(--xxl-padding);
  }
}
.wrapper {
  width: 100%;
  padding-right: var(--sm-padding);
  padding-left: var(--sm-padding);
}
@media (min-width: 1280px) {
  .wrapper {
    max-width: 1920px;
    margin-right: auto;
    margin-left: auto;
  }
}
@media (min-width: 768px) {
  .wrapper {
    padding-right: var(--md-padding);
    padding-left: var(--md-padding);
  }
}
@media (min-width: 1280px) {
  .wrapper {
    padding-right: var(--xl-padding);
    padding-left: var(--xl-padding);
  }
}
@media (min-width: 1560px) {
  .wrapper {
    margin-right: auto;
    margin-left: auto;
    padding-right: var(--xxl-padding);
    padding-left: var(--xxl-padding);
  }
}
.font-basic {
  font-size: 16px;
  line-height: 117.2%;
  font-weight: 400;
}
.font-basic b {
  font-weight: 700;
}
.font-title {
  font-size: 30px;
}
.font-special {
  font-size: 18px;
  line-height: 150%;
  font-weight: 400;
}
.font-special b {
  font-weight: 700;
}
.logo {
  width: 127px;
  height: 33px;
}
.logo--footer {
  width: 140px;
  height: 36px;
}
@media (min-width: 768px) {
  .logo {
    width: 173px;
    height: 45px;
  }
  .logo--footer {
    width: 140px;
    height: 36px;
  }
}
.logo svg {
  width: 100%;
  height: 100%;
}
.communication-list {
  display: flex;
  gap: 10px;
}
@media (min-width: 1280px) {
  .communication-list {
    gap: 15px;
  }
}
.communication-list__link {
  position: relative;
  display: flex;
}
@media (min-width: 768px) {
  .communication-list__link {
    align-items: center;
    column-gap: 4px;
  }
}
@media (min-width: 1280px) {
  .communication-list__link {
    justify-content: center;
    column-gap: 15px;
    width: 140px;
    min-height: 46px;
    background-color: #ffffff38;
  }
}
.communication-list__icon {
  flex-shrink: 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
}
.communication-list__icon::before {
  position: absolute;
  inset: 0;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
}
.communication-list__link--viber .communication-list__icon {
  background: #7C509A;
}
.communication-list__link--viber .communication-list__icon::before {
  background-image: url("../../images/viber.svg");
  background-size: 18px 19px;
}
.communication-list__link--telegram .communication-list__icon {
  background-color: #039BE5;
}
.communication-list__link--telegram .communication-list__icon::before {
  background-image: url("../../images/telegram.svg");
  background-size: 19px 15px;
}
.communication-list__name {
  display: none;
}
@media (min-width: 768px) {
  .communication-list__name {
    display: block;
    font-size: 14px;
  }
}
.phone {
  position: relative;
  padding-left: 24px;
}
.phone::before {
  position: absolute;
  inset: 0;
  content: "";
  width: 19px;
  height: 19px;
  background-color: var(--color-accent);
  mask-image: url("../../images/phone.svg");
  mask-size: cover;
  mask-repeat: no-repeat;
  mask-position: center;
}
@media (min-width: 768px) {
  .phone {
    padding-left: 28px;
  }
}
.app-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  min-height: 46px;
  color: var(--color-font-extra-dark);
  background: linear-gradient(180deg, #FFDC83 0%, #FBD26A 25%, #C17024 84.9%, #E3A548 100%);
  box-shadow: 0px 0px 48px 0px #F0C05C8A;
}
.connection__btn {
  display: none;
}
@media (min-width: 768px) {
  .connection__btn {
    display: flex;
  }
}
.connection__phone {
  font-size: 14px;
}
@media (min-width: 768px) {
  .connection__phone {
    font-size: 18px;
  }
}
.contacts-list {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  line-height: 174%;
}
.contacts-list__link-name {
  display: none;
}
@media (min-width: 1280px) {
  .contacts-list__link-name {
    display: inline;
  }
}
.contacts-list__link {
  position: relative;
  padding-left: 39px;
}
.contacts-list__link::before {
  position: absolute;
  inset: 0;
  content: "";
  width: 20px;
  height: 20px;
  background-color: var(--color-accent);
  mask-size: cover;
  mask-repeat: no-repeat;
  mask-position: center;
}
.contacts-list__link--adress::before {
  mask-image: url("../../images/map.svg");
}
.contacts-list__link--email::before {
  mask-image: url("../../images/message.svg");
}
.contacts-list__link--phone::before {
  mask-image: url("../../images/phone.svg");
}
.site-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
  font-size: 24px;
  font-weight: 700;
}
.site-list__sublist {
  display: flex;
  flex-direction: column;
  gap: 11px;
  margin-top: 35px;
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 400;
}
.site-list__link:hover,
.site-list__sublink:hover {
  color: var(--color-accent);
}
.social-list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}
@media (min-width: 768px) {
  .social-list {
    row-gap: 21px;
  }
}
@media (min-width: 1560px) {
  .social-list {
    gap: 40px;
  }
}
.social-list__item {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: 15px;
}
@media (min-width: 1560px) {
  .social-list__item:last-of-type {
    order: -1;
    flex-grow: 1;
  }
}
@media (min-width: 1280px) {
  .social-list__item {
    gap: 25px;
  }
}
.social-list__link {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.social-list__link::before {
  position: absolute;
  inset: 0;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
}
.social-list__link--facebook {
  background-color: #3B5998;
}
.social-list__link--facebook::before {
  background-image: url("../../images/facebook.svg");
  background-size: 13px 27px;
}
.social-list__link--instagram {
  background: linear-gradient(43.94deg, #EEA054 16.16%, #D64763 50.11%, #B62A99 83.68%);
}
.social-list__link--instagram::before {
  background-image: url("../../images/instagram.svg");
  background-size: 17px 17px;
}
.social-list__link--youtube {
  background-color: var(--color-white);
}
.social-list__link--youtube::before {
  border-radius: 50%;
  background-image: url("../../images/youtube.svg");
  background-size: 40px 40px;
}
@media (min-width: 768px) {
  .social-list__description {
    font-size: 18px;
  }
}
.page {
  min-width: 375px;
  height: 100%;
  margin: 0 auto;
  font-family: var(--font-page);
  font-size: 16px;
  line-height: 117.2%;
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  color: var(--color-font-basic);
  background-color: var(--color-back-basic);
}
.page__main {
  flex-grow: 1;
}
.burger-btn {
  position: relative;
  width: 22px;
  height: 22px;
}
.burger-btn::before {
  position: absolute;
  right: 0;
  transform: translateY(-50%);
  width: 22px;
  height: 2px;
  margin: auto 0;
  background-color: var(--color-font-light);
  box-shadow: 0 calc((-18px) / 2 + 1px) var(--color-font-light), 0 calc(18px / 2 - 1px) var(--color-font-light);
  content: "";
}
@media (min-width: 768px) {
  .burger-btn {
    width: 35px;
    height: 35px;
  }
  .burger-btn::before {
    position: absolute;
    right: 0;
    transform: translateY(-50%);
    width: 35px;
    height: 2px;
    margin: auto 0;
    background-color: var(--color-font-light);
    box-shadow: 0 calc((-28px) / 2 + 1px) var(--color-font-light), 0 calc(28px / 2 - 1px) var(--color-font-light);
    content: "";
  }
}
.main-nav {
  min-height: 136px;
  padding-top: 22px;
  padding-bottom: 28px;
  color: var(--color-font-light);
  background-color: var(--color-back-extra-dark);
}
@media (min-width: 768px) {
  .main-nav {
    padding-top: 28px;
    padding-bottom: 19px;
  }
}
@media (min-width: 1280px) {
  .main-nav {
    padding-top: 28px;
    padding-bottom: 31px;
  }
}
.main-nav__top {
  position: relative;
  display: grid;
  grid-template-columns: min-content min-content 1fr min-content;
  grid-template-rows: auto auto;
  align-items: center;
  gap: 13px 10px;
}
@media (min-width: 768px) {
  .main-nav__top {
    display: flex;
    align-items: start;
    gap: 0 20px;
  }
}
@media (min-width: 1280px) {
  .main-nav__top {
    gap: 0 55px;
  }
}
@media (min-width: 1280px) {
  .main-nav__top {
    gap: 0 70px;
  }
}
@media (min-width: 1560px) {
  .main-nav__top {
    gap: 0 85px;
  }
}
.main-nav__head {
  display: contents;
}
@media (min-width: 768px) {
  .main-nav__head {
    display: flex;
    flex-direction: column;
    gap: 7px;
  }
}
@media (min-width: 1280px) {
  .main-nav__head {
    flex-direction: row;
    gap: 22px;
  }
}
.main-nav__site-menu-open-btn {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}
@media (min-width: 768px) {
  .main-nav__site-menu-open-btn {
    margin-top: 3px;
  }
}
@media (min-width: 1280px) {
  .main-nav__site-menu-open-btn {
    position: absolute;
    left: 35px;
  }
}
.main-nav__logo {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
}
.main-nav__slogan {
  grid-column: 2 / -2;
  grid-row: 2 / -1;
  align-self: center;
  font-size: 12px;
}
.main-nav__slogan b {
  font-size: 18px;
}
.main-nav__slogan span {
  color: var(--color-accent);
}
@media (min-width: 1280px) {
  .main-nav__slogan {
    font-size: 14px;
  }
}
.main-nav__social {
  grid-column: -2 / -1;
  grid-row: 2 / -1;
}
@media (min-width: 768px) {
  .main-nav__social {
    margin-top: 3px;
    margin-left: 9px;
  }
}
@media (min-width: 1280px) {
  .main-nav__social {
    margin-top: 2px;
    margin-left: 0;
  }
}
.main-nav__social-description {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__social-description {
    display: block;
    margin-bottom: 17px;
    font-size: 12px;
    color: var(--color-accent);
  }
}
@media (min-width: 1280px) {
  .main-nav__social-description {
    margin-bottom: 10px;
    font-size: 14px;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .main-nav__communication-list {
    justify-content: center;
  }
}
.main-nav__connection {
  grid-column: -3 / -1;
  grid-row: 1 / 2;
  justify-self: end;
}
@media (min-width: 768px) {
  .main-nav__connection {
    margin-left: auto;
    margin-top: 2px;
  }
}
@media (min-width: 1280px) {
  .main-nav__connection {
    margin-left: 0;
    margin-top: 3px;
  }
}
@media (min-width: 1560px) {
  .main-nav__connection {
    margin-left: auto;
  }
}
@media (min-width: 768px) {
  .main-nav__phone {
    margin-bottom: 10px;
  }
}
@media (min-width: 768px) {
  .main-nav__phone {
    margin-bottom: 9px;
  }
}
.main-nav__site-menu {
  display: none;
}
.page-footer {
  padding-top: 60px;
  padding-bottom: 60px;
  color: var(--color-white-050);
  background-color: var(--color-back-dark);
}
@media (min-width: 768px) {
  .page-footer {
    padding-top: 82px;
    padding-bottom: 77px;
  }
}
@media (min-width: 992px) {
  .page-footer {
    padding-top: 75px;
    padding-bottom: 70px;
  }
}
.page-footer__wrapper {
  display: grid;
  gap: 20px;
  padding-left: 46px;
  padding-right: 46px;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto 1fr;
    align-items: start;
    gap: 31px 34px;
    padding-right: var(--sm-padding);
    padding-left: var(--sm-padding);
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .page-footer__wrapper {
    padding-right: var(--md-padding);
    padding-left: var(--md-padding);
  }
}
@media (min-width: 768px) and (min-width: 1280px) {
  .page-footer__wrapper {
    padding-right: var(--xl-padding);
    padding-left: var(--xl-padding);
  }
}
@media (min-width: 768px) and (min-width: 1560px) {
  .page-footer__wrapper {
    margin-right: auto;
    margin-left: auto;
    padding-right: var(--xxl-padding);
    padding-left: var(--xxl-padding);
  }
}
@media (min-width: 1280px) {
  .page-footer__wrapper {
    grid-template-columns: min-content 1fr max-content;
    gap: 24px 40px;
  }
}
@media (min-width: 1560px) {
  .page-footer__wrapper {
    grid-template-columns: auto auto auto auto;
    gap: 52px;
    padding-left: 46px;
  }
}
.page-footer__logo {
  margin-bottom: 6px;
}
@media (min-width: 768px) {
  .page-footer__logo {
    margin-bottom: 0;
  }
}
.page-footer__site-list {
  position: relative;
  padding-bottom: 36px;
}
.page-footer__site-list::after {
  position: absolute;
  content: "";
  bottom: 6px;
  left: 0;
  right: 0;
  width: 100%;
  height: 1px;
  background-color: #2e2a22;
}
@media (min-width: 768px) {
  .page-footer__site-list {
    grid-column: 2 / 3;
    grid-row: 1 / -1;
    padding-bottom: 0;
  }
  .page-footer__site-list::after {
    display: none;
  }
}
@media (min-width: 1280px) {
  .page-footer__site-list {
    display: grid;
    grid-template-columns: max-content max-content;
    grid-template-rows: min-content min-content min-content 1fr;
    grid-auto-rows: min-content;
    gap: 37px 32px;
  }
}
@media (min-width: 1280px) {
  .page-footer__site-list li:first-of-type {
    grid-column: 1 / 2;
    grid-row: 1 / -1;
  }
}
@media (min-width: 1280px) {
  .page-footer__site-list li:not(:first-of-type) {
    grid-column: 2 / span 1;
  }
}
.page-footer__contacts {
  display: flex;
  flex-direction: column;
}
.page-footer__contacts-title {
  margin-bottom: 25px;
  font-size: 24px;
  font-weight: 700;
}
.page-footer__contacts-list {
  margin-left: 5px;
}
.page-footer__social-list {
  margin-top: 6px;
}
@media (min-width: 768px) {
  .page-footer__social-list {
    grid-column: -2 / -1;
    grid-row: -2 / -1;
    margin-top: 0;
  }
}
@media (min-width: 1560px) {
  .page-footer__social-list {
    grid-column: -2 / -1;
    grid-row: 1 / 2;
  }
}
